import React from 'react';
import './todo-list-item.css';

export default class TodoListItem extends React.Component {

    render() {

        let classNames = "";
        
        if (this.props.done) {
            classNames += " done";
        }
        if (this.props.important) {
            classNames += " important";
        }

        return (
            <div>
                <span className={classNames} onClick={this.props.onToggleDone}>{this.props.label}</span>
                <button onClick={this.props.onToggleImportant} type="button" className="btn btn-outline-primary btn-sm pull-right exc-but">
                    <i className="fa fa-fw fa-exclamation text-success" aria-hidden="true"></i>
                </button>
                <button onClick={this.props.onDeleted} type="button" className="btn btn-outline-primary btn-sm pull-right trash-but">
                    <i className="fa fa-fw fa-trash-o text-danger" aria-hidden="true"></i>
                </button>
            </div>
        );
    }
}