import React from 'react';
import './search-panel.css';

export default class SearchPanel extends React.Component { 

    buttons = [
        {name:"all",label:"All"},
        {name:"active",label:"Active"},
        {name:"done",label:"Done"}
    ];

    render(){
        const { onSearchTextChange, filterProperty, onSearchPropertyChange } = this.props;
        const buttons = this.buttons.map(({name, label})=>{
            const isActive = filterProperty === name;
            const clazz = isActive ? "btn-info" : "btn-outline-secondary";
            return (
                <button type="button" 
                        className={`btn ${clazz} btn-sm`}
                        onClick={()=>{onSearchPropertyChange(name)}}
                        key={name}>{label}</button>
            );
        });
        return (
            <div className="search-panel">
                <div className="input-group">
                    <input onChange={onSearchTextChange} type="text" placeholder="search" className="form-control search-input"/>
                    <div className="btn-group search-buttons" role="group" aria-label="Basic example">
                        {buttons}
                    </div>
                </div>
                
            </div>
        );
    }
}