import React from 'react';
import './add-item.css';

export default class AddItem extends React.Component {
    constructor() {
        super();

        this.state = {
            label: ""
        }

        this.onLabelChange = (e) => {
            this.setState({
                label: e.target.value
            });
        }

        this.onSubmit = (e) => {
            e.preventDefault();
            this.props.onItemAdded(this.state.label);
        }
    }

    render() {
        return (
            <form onSubmit={this.onSubmit} className="d-flex add-form">
                    <input className="form-control" onChange={this.onLabelChange} type="text"/>
                    <button className="btn btn-outline-primary">Add item</button>
            </form>
        );
    }
}