import React from 'react';
import ReactDOM from 'react-dom';
import AppHeader from './components/app-header';
import TodoList from './components/todo-list';
import SearchPanel from './components/search-panel';
import AddItem from './components/add-item';
import './index.css';

export default class App extends React.Component {

    constructor() {
        super();

        this.maxId = 0;

        this.state = {
            todoData: [
                this.createTodoItem("Drink Coffee"),
                this.createTodoItem("Play games"),
                this.createTodoItem("Make awesome app")
            ],
            filterText: "",
            filterProperty: "all" // all, active, done
        };

        this.ToggleProperty = (arr,id,propName) => {
            const idx = arr.findIndex((el)=>{
                return el.id === id;
            });

            const item = arr[idx];
            const newItem = {...item,[propName]:!item[propName]};
            return [...arr.slice(0,idx),newItem,...arr.slice(idx+1,arr.length)];
        }

        this.deleteItem = (id) => {
            this.setState((state)=>{
                const idx = state.todoData.findIndex((el)=>{
                    return el.id === id;
                });
                const newData = [...state.todoData.slice(0,idx),...state.todoData.slice(idx+1,state.todoData.length)];
                return {
                    todoData: newData
                };
            });
        }

        this.onItemAdded = (text) => {
            this.setState(({todoData})=>{
                return {
                    todoData: [...todoData,this.createTodoItem(text)]
                }
            });
        }

        this.onToggleImportant = (id) => {
            this.setState(state=>{
                return {
                    todoData: this.ToggleProperty(this.state.todoData,id,"important")
                };

            });
        }

        this.onToggleDone = (id) => {
            this.setState(state=>{
                return {
                    todoData: this.ToggleProperty(this.state.todoData,id,"done")
                };

            });
        };

        this.onSearchTextChange = (e) => {
            this.setState({filterText: e.target.value});
        };

        this.onSearchPropertyChange = (name) => {
            this.setState({ filterProperty: name });
        }
    }

    createTodoItem(label) {
        return {
            label,
            important:false,
            done:false,
            id:++this.maxId
        }
    };

    filterByText(items,text) {
        if (text.length===0) {
            return items;
        }

        return items.filter(el=>el.label.toLowerCase().includes(text.toLowerCase()));
    };

    filterByProperty(items,property) {
        switch (property) {
            case "all":
                return items;
            case "active":
                return items.filter(el=>!el.done);
            case "done":
                return items.filter(el=>el.done);
            default:
                return items;
        }
    }

    render() {
        const doneCount = this.state.todoData.filter(el=>el.done).length;
        const todoCount = this.state.todoData.length - doneCount;
        const visibleItems = this.filterByProperty(this.filterByText(this.state.todoData,this.state.filterText),this.state.filterProperty);
        return (
            <div>
                <AppHeader todoCount={todoCount} doneCount={doneCount}/>
                <SearchPanel onSearchTextChange={this.onSearchTextChange}
                             onSearchPropertyChange={this.onSearchPropertyChange}
                             filterProperty={this.state.filterProperty}/>
                <TodoList items={visibleItems}
                          onDeleted={this.deleteItem}
                          onToggleDone={this.onToggleDone}
                          onToggleImportant={this.onToggleImportant}/>
                <AddItem onItemAdded={this.onItemAdded}/>
            </div>
        );
    }


};

ReactDOM.render(<App/>,document.getElementById("root"));